<template>
  <v-container class="full-height">
    <v-row dense>
      <v-col lg="4" offset-lg="4">
        <div class="authorize d-flex justify-center align-center">
          <v-sheet elevation="6" class="px-2 py-2 rounded">
            <div class="authorize__form">
              <span class="font-weight-bold">Авторизация</span>
              <LoginForm @login="login" />
            </div>
          </v-sheet>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";
import LoginForm from "../components/Forms/Login";

export default {
  name: "Login",
  metaInfo: {
    title: "Шестерёнка | Авторизация"
  },
  components: {
    LoginForm
  },
  methods: {
    ...mapActions({
      authorize: "fetchLogin"
    }),
    async login(data) {
      try {
        await this.authorize(data);
        await this.$router.push({ name: "Orders" });
      } catch (err) {
        console.error(err);
      }
    }
  }
};
</script>

<style scoped>
.authorize {
  height: 100vh;
}
</style>
