<template>
  <v-form ref="form" @submit.prevent="toggle">
    <v-text-field
      v-model="form.login"
      :rules="rules"
      label="Ваш логин"
      placeholder="Логин"
      required
      class="mb-3"
    ></v-text-field>
    <v-text-field
      v-model="form.password"
      :rules="rules"
      label="Ваш пароль"
      placeholder="Пароль"
      type="password"
      required
      class="mb-3"
    ></v-text-field>
    <v-btn type="submit" depressed color="primary">
      Войти
    </v-btn>
  </v-form>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      form: {
        login: "",
        password: ""
      },
      rules: [v => v.length > 5 || "Количество символово должно быть больше 5"]
    };
  },
  methods: {
    toggle() {
      if (this.$refs.form.validate()) {
        this.$emit("login", this.form);
      }
    }
  }
};
</script>

<style scoped></style>
